import axios from 'axios';
import { all ,fork, call, put, takeEvery, takeLatest, delay, throttle} from 'redux-saga/effects';
import { 
  ADD_POST_REQUEST, ADD_POST_SUCCESS, ADD_POST_FAILURE,
  ADD_COMMENT_REQUEST, ADD_COMMENT_SUCCESS, ADD_COMMENT_FAILURE, 
  REMOVE_POST_REQUEST, REMOVE_POST_SUCCESS, REMOVE_POST_FAILURE, 
  LOAD_POSTS_REQUEST, LOAD_POSTS_SUCCESS, LOAD_POSTS_FAILURE, 
  LIKE_POST_REQUEST, UNLIKE_POST_REQUEST, LIKE_POST_SUCCESS, 
  LIKE_POST_FAILURE, UNLIKE_POST_SUCCESS, UNLIKE_POST_FAILURE,
  UPLOAD_IMAGES_FAILURE, UPLOAD_IMAGES_REQUEST, UPLOAD_IMAGES_SUCCESS, 
  RETWEET_SUCCESS, RETWEET_FAILURE, RETWEET_REQUEST, 
  LOAD_POST_REQUEST, LOAD_POST_SUCCESS, LOAD_POST_FAILURE, 
  LOAD_USER_POSTS_REQUEST, LOAD_HASHTAG_POSTS_REQUEST, LOAD_USER_POSTS_FAILURE, 
  LOAD_USER_POSTS_SUCCESS, LOAD_HASHTAG_POSTS_SUCCESS, LOAD_HASHTAG_POSTS_FAILURE,
} from '../reducers/post';
import { ADD_POST_TO_ME, CHANGE_NICKNAME_FAILURE, CHANGE_NICKNAME_REQUEST, CHANGE_NICKNAME_SUCCESS, LOAD_USER_SUCCESS, REMOVE_POST_OF_ME } from '../reducers/user';

function loadUserPostsAPI(data,lastId) {
  return axios.get(`/user/${data}/posts?lastId=${lastId || 0}`);
}

function* loadUserPosts(action) {
  try {
    const result = yield call(loadUserPostsAPI, action.data, action.lastId);
    yield put({       
      type: LOAD_USER_POSTS_SUCCESS, 
      data: result.data
    })
  } catch (err) {
    console.error(err);
    yield put({
      type: LOAD_USER_POSTS_FAILURE,
      error : err.response.data
    })
  }   
}

function loadHashtagPostsAPI(data,lastId) {
  return axios.get(`/hashtag/${encodeURIComponent(data)}?lastId=${lastId || 0}`);
}

function* loadHashtagPosts(action) {
  try {
    const result = yield call(loadHashtagPostsAPI, action.data, action.lastId);
    yield put({       
      type: LOAD_HASHTAG_POSTS_SUCCESS, 
      data: result.data
    })
  } catch (err) {
    console.error(err);
    yield put({
      type: LOAD_HASHTAG_POSTS_FAILURE,
      error : err.response.data
    })
  }   
}

function loadPostsAPI(lastId) {
  return axios.get(`/posts?lastId=${lastId || 0}`);
}

function* loadPosts(action) {
  try {
    const result = yield call(loadPostsAPI, action.lastId);
    yield put({       
      type: LOAD_POSTS_SUCCESS, 
      data: result.data
    })
  } catch (err) {
    console.error(err);
    yield put({
      type: LOAD_POSTS_FAILURE,
      error : err.response.data
    })
  }   
}

function loadPostAPI(data) {
  return axios.get(`/post/${data}`);
}

function* loadPost(action) {
  try {
    const result = yield call(loadPostAPI, action.data);
    yield put({       
      type: LOAD_POST_SUCCESS, 
      data: result.data
    })
  } catch (err) {
    console.error(err);
    yield put({
      type: LOAD_POST_FAILURE,
      error : err.response.data
    })
  }  
}

function addPostAPI(data) {
  return axios.post('/post',  data);
}

function* addPost(action) {
  try {
    const result =  yield call(addPostAPI, action.data);
    yield put({       
      type: ADD_POST_SUCCESS, 
      data: result.data,
    })
    yield put({
      type: ADD_POST_TO_ME,
      data: result.data.id,
    }) 
  } catch (err) {
    yield put({
      type: ADD_POST_FAILURE,
      error : err.response.data
    })
  }  
}

function addCommentAPI(data) {
  return axios.post(`/post/${data.postId}/comment`, data);
}

function* addComment(action) {
  try {
    const result =  yield call(addCommentAPI, action.data);
    console.log(result);
    yield put({       
      type: ADD_COMMENT_SUCCESS, 
      data: result.data
    }) 
  } catch (err) {
    console.error(err);
    yield put({
      type: ADD_COMMENT_FAILURE,
      error : err.response.data
    })
  }  
}

